.juices {
    max-width: 60rem;
    width: 90%;
    margin: 2rem auto;
    animation: juices-appear 1s ease-out forwards;
  }
  
  .juices ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .loading{
    text-align: center;
  }
  @keyframes juices-appear {
    from {
      opacity: 0;
      transform: translateY(3rem);
    }
  
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }